// =============================================================================
// Document
// =============================================================================


html,
body {
    background: $background-color;
}

html {
    background-position: fixed;
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}

*,
*:before,
*:after {
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    box-sizing: inherit;
}

body {
    color: $base-font-color;
    margin: 0;
}
